import Vue from "vue";
import Vuex from "vuex";

import axios from "./axios-common.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    boardlist: [],
    board: null,
    sidolist: [],
    gugunlist: [],
    donglist: [],
    aptlist: [],
    aptdetail: null,
    checkexist: false,
    checkmember: false,
    checklogin: false,
    user: null,
  },

  getters: {
    boardlist(state) {
      //todolist return
      return state.boardlist;
    },

    board(state) {
      return state.board;
    },

    sidolist(state) {
      return state.sidolist;
    },

    gugunlist(state) {
      return state.gugunlist;
    },

    donglist(state) {
      return state.donglist;
    },

    aptlist(state) {
      return state.aptlist;
    },

    aptdetail(state) {
      return state.aptdetail;
    },

    checkexist(state) {
      return state.checkexist;
    },

    user(state) {
      return state.user;
    },

    checklogin(state) {
      return state.checklogin;
    },

    checkmember(state) {
      return state.checkmember;
    },
  },

  actions: {
    DROPOUT: (store, payload) => {
      //console.log(payload);
      return axios
        .post("/dropOut", {
          id: payload.id,
          pass: payload.pass,
        })
        .then(() => {
          localStorage.clear();
          store.commit("LOGINFAIL", { check: false });
        })
        .catch((exp) => alert("  ." + exp));
    },
    CHECKMEMBER: (store, payload) => {
      //console.log(payload);
      return axios
        .post("/loginProcess", {
          id: payload.id,
          pass: payload.pass,
        })
        .then((response) => {
          if (response.data == "") {
            store.commit("CHECKMEMBER", { check: false });
          } else {
            axios
              .post("/dropOut", {
                id: payload.id,
                pass: payload.pass,
              })
              .then(() => {
                localStorage.clear();
                location.href = "/";
              })
              .catch((exp) => alert(" ." + exp));
          }
        })
        .catch((exp) => alert("   ." + exp));
    },
    FINDPASS: (store, payload) => {
      //console.log(payload);
      axios
        .post("/findPass", {
          name: payload.name,
          id: payload.id,
          tel: payload.tel,
        })
        .then((response) => {
          if (response.data == "") {
            store.commit("LOGINFAIL", { check: false });
          } else {
            console.log(response.data);
            store.commit("LOGIN", { user: response.data });
          }
        })
        .catch((exp) => alert("  ." + exp));
    },
    SIGNUP: (store, payload) => {
      //console.log(payload);
      return axios
        .post("/signUpProcess", {
          id: payload.id,
          pass: payload.pass,
          name: payload.name,
          email: payload.email,
          tel: payload.tel,
          car: payload.car,
          pet: payload.pet,
          scoreCheck: payload.scoreCheck,
          zip: payload.zip,
          address_base: payload.address_base,
          address_detail: payload.address_detail,
          office_zip: payload.office_zip,
          office_address_base: payload.address_base,
          office_address_detail: payload.address_detail,
        })
        .then(() => {
          console.log(" .");
          store.commit("LOGIN", { user: payload });
        })
        .catch((exp) => alert(" ." + exp));
    },
    LOGIN: (store, payload) => {
      //console.log(payload);
      return axios
        .post("/loginProcess", {
          id: payload.id,
          pass: payload.pass,
        })
        .then((response) => {
          if (response.data == "") {
            store.commit("LOGINFAIL", { check: false });
          } else {
            console.log(response.data);
            store.commit("LOGIN", { user: response.data });
          }
        })
        .catch((exp) => alert(" ." + exp));
    },

    UPDATEPROFILE: (store, payload) => {
      //console.log(payload);
      axios
        .post("/modifyinfo", {
          id: payload.id,
          pass: payload.pass,
          name: payload.name,
          email: payload.email,
          tel: payload.tel,
          car: payload.car,
          pet: payload.pet,
          scoreCheck: payload.scoreCheck,
          zip: payload.zip,
          address_base: payload.address_base,
          address_detail: payload.address_detail,
          office_zip: payload.office_zip,
          office_address_base: payload.address_base,
          office_address_detail: payload.address_detail,
        })
        .then(() => {
          console.log(" .");
          store.commit("LOGIN", { user: payload });
        })
        .catch((exp) => alert("  ." + exp));
    },

    CEHCKEXIST: (store, payload) => {
      axios
        .get("/getMember/" + payload.id)
        .then((response) => {
          store.commit("CEHCKEXIST", { checkexist: response.data });
          console.log(response.data);
        })
        .catch((response) => {
          console.log(response);
        });
    },

    UPGRADEDETAIL: (store, payload) => {
      return axios
        .post("/detail/" + payload.dong + "/" + payload.aptName, {
          id: payload.id,
          car: payload.car,
          pet: payload.pet,
          scoreCheck: payload.scoreCheck,
          office_address_base: payload.office_address_base,
          office_zip: payload.office_zip,
        })
        .then((response) => {
          store.commit("UPGRADEDETAIL", { aptdetail: response.data });
          console.log(response.data);
        })
        .catch((response) => {
          console.log(response);
        });
    },

    DETAIL: (store, payload) => {
      return axios
        .get("/detail/" + payload.dong + "/" + payload.aptName)
        .then((response) => {
          store.commit("DETAIL", { aptdetail: response.data });
          console.log(response.data);
        })
        .catch((response) => {
          console.log(response);
        });
    },

    SEARCH: (store, payload) => {
      axios
        .post("/search/" + payload.dong)
        .then((response) => {
          store.commit("SEARCH", { aptlist: response.data });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    ALLSIDO: (store) => {
      axios
        .get("/address")
        .then((response) => {
          store.commit("ALLSIDO", { sidolist: response.data });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    UPDATEGUGUN: (store, payload) => {
      axios
        .get("/address/gugun/" + payload.sido)
        .then((response) => {
          store.commit("UPDATEGUGUN", { gugunlist: response.data });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    UPDATEDONG: (store, payload) => {
      axios
        .get("/address/dong/" + payload.gugun)
        .then((response) => {
          store.commit("UPDATEDONG", { donglist: response.data });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    ALLBOARD: (store) => {
      axios
        .get("/board")
        .then((response) => {
          store.commit("ALLBOARD", { boardlist: response.data });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    DELETEBOARD: (store, payload) => {
      axios
        .delete("/board/" + payload.no)
        .then(() => {
          console.log(" !");
          store.dispatch("ALLBOARD");
        })
        .catch((response) => {
          console.log(response);
        });
    },

    SHOWBOARD: (store, payload) => {
      return axios
        .get("/board/" + payload.no)
        .then((response) => {
          console.log(" .");
          store.commit("SHOWBOARD", { board: response.data }); //call mutation
        })
        .catch((exp) => alert("  ." + exp));
    },

    ADDBOARD: (store, payload) => {
      //console.log(payload);
      axios
        .post("/board", {
          id: payload.id,
          title: payload.title,
          content: payload.content,
        })
        .then(() => {
          console.log(" .");
          store.dispatch("ALLBOARD"); //call action
        })
        .catch((exp) => alert("  ." + exp));
    },

    UPDATEBOARD: (store, payload) => {
      axios
        .put("/board", {
          no: payload.no,
          title: payload.title,
          content: payload.content,
        })
        .then(() => {
          console.log(" .");
          store.dispatch("ALLBOARD"); //call action
        })
        .catch((exp) => alert("  ." + exp));
    },
  },
  // .  (state, payload)   
  mutations: {
    ALLBOARD: (state, payload) => {
      state.boardlist = payload.boardlist;
    },
    SHOWBOARD: (state, payload) => {
      state.board = payload.board;
    },
    ALLSIDO: (state, payload) => {
      state.sidolist = payload.sidolist;
    },
    UPDATEGUGUN: (state, payload) => {
      state.gugunlist = payload.gugunlist;
    },
    UPDATEDONG: (state, payload) => {
      state.donglist = payload.donglist;
    },
    SEARCH: (state, payload) => {
      state.aptlist = payload.aptlist;
    },
    DETAIL: (state, payload) => {
      state.aptdetail = payload.aptdetail;
    },
    UPGRADEDETAIL: (state, payload) => {
      state.aptdetail = payload.aptdetail;
    },
    CEHCKEXIST: (state, payload) => {
      state.checkexist = payload.checkexist;
    },
    LOGIN: (state, payload) => {
      state.user = payload.user;
      localStorage.setItem("id", payload.user.id);
      localStorage.setItem("name", payload.user.name);
      localStorage.setItem("email", payload.user.email);
      localStorage.setItem("tel", payload.user.tel);
      localStorage.setItem("car", payload.user.car);
      localStorage.setItem("pet", payload.user.pet);
      localStorage.setItem("scoreCheck", payload.user.scoreCheck);
      localStorage.setItem("zip", payload.user.zip);
      localStorage.setItem("address_base", payload.user.address_base);
      localStorage.setItem("address_detail", payload.user.address_detail);
      localStorage.setItem("office_zip", payload.user.office_zip);
      localStorage.setItem("office_address_base", payload.user.office_address_base);
      localStorage.setItem("office_address_detail", payload.user.office_address_detail);
      state.checklogin = true;
    },
    LOGINFAIL: (state, payload) => {
      state.checklogin = payload.check;
    },
    CHECKMEMBER: (state, payload) => {
      state.checkmember = payload.check;
    },
  },
});
