<template>
  <div id="signup">
    <sign-up-header></sign-up-header>
    <b-row>
      <b-col cols="3"></b-col>
      <b-col cols="6">
        <b-card bg-variant="light" id="sign-card">
          <b-form-group label="ID:" label-for="id-Input" label-cols-sm="3">
            <b-form-input
              v-model="id"
              placeholder="Enter your ID"
              :state="idState"
              aria-describedby="input-id-feedback"
            ></b-form-input>

            <b-form-invalid-feedback id="input-id-feedback">
              ID .
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group label="Password:" label-for="password-Input" label-cols-sm="3">
            <b-form-input
              v-model="pass"
              placeholder="Enter your Password"
              type="password"
              :state="passState"
              aria-describedby="input-pass-feedback"
            ></b-form-input>

            <b-form-invalid-feedback id="input-pass-feedback">
               .
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group label="password:" label-for="password-Input" label-cols-sm="3">
            <b-form-input
              v-model="pass2"
              placeholder="Enter your Password"
              type="password"
              :state="pass2State"
              aria-describedby="input-pass2-feedback"
              trim
            >
            </b-form-input>

            <b-form-invalid-feedback id="input-pass2-feedback">
               .
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group label="Name:" label-for="name-Input" label-cols-sm="3">
            <b-form-input v-model="name" placeholder="Enter your Name"></b-form-input>
          </b-form-group>

          <b-form-group label="E-mail:" label-for="email-Input" label-cols-sm="3">
            <b-form-input v-model="email" placeholder="Enter your E-mail"></b-form-input>
          </b-form-group>

          <b-form-group label="Address:" label-for="address-Input" label-cols-sm="3">
            <b-form-input
              v-model="zip"
              placeholder=" "
              disabled
              style="margin-bottom: 1%"
              >{{ zip }}</b-form-input
            >
            <b-form-input
              v-model="address_base"
              placeholder=" "
              disabled
              style="margin-bottom: 1%"
              >{{ address_base }}</b-form-input
            >
            <b-form-input
              v-model="address_detail"
              placeholder=" "
              style="margin-bottom: 1%"
              >{{ address_detail }}</b-form-input
            >
            <b-button @click="AddressApi(1)"> </b-button>
          </b-form-group>

          <b-form-group label="Tel:" label-for="Tel-Input" label-cols-sm="3">
            <b-form-input v-model="tel" placeholder="Enter your Tel"></b-form-input>
          </b-form-group>

          <hr />

          <h5 id="select-info">  </h5>

          <b-form-group label="   " label-for="info-Input" label-cols-sm="3">
            <b-row>
              <b-col cols="2"></b-col>
              <b-col cols="4">
                <b-form-radio v-model="scoreCheck" name="score-radios" value="Y">Y</b-form-radio>
              </b-col>
              <b-col cols="1">
                <b-form-radio v-model="scoreCheck" name="score-radios" value="N">N</b-form-radio>
              </b-col>
              <b-col cols="5"></b-col>
            </b-row>
          </b-form-group>

          <hr />

          <b-form-group label="Pet:" label-for="Pet-Input" label-cols-sm="3">
            <b-row>
              <b-col cols="2"></b-col>
              <b-col cols="4">
                <b-form-radio v-model="pet" name="pet-radios" value="Y">Y</b-form-radio>
              </b-col>
              <b-col cols="1">
                <b-form-radio v-model="pet" name="pet-radios" value="N">N</b-form-radio>
              </b-col>
              <b-col cols="5"></b-col>
            </b-row>
          </b-form-group>

          <hr />

          <b-form-group label="Car:" label-for="Car-Input" label-cols-sm="3">
            <b-row>
              <b-col cols="2"></b-col>
              <b-col cols="4">
                <b-form-radio v-model="car" name="car-radios" value="Y">Y</b-form-radio>
              </b-col>
              <b-col cols="1">
                <b-form-radio v-model="car" name="car-radios" value="N">N</b-form-radio>
              </b-col>
              <b-col cols="5"></b-col>
            </b-row>
          </b-form-group>

          <hr />

          <b-form-group label="Office:" label-for="Office-Input" label-cols-sm="3">
            <b-form-input
              v-model="office_zip"
              placeholder=" "
              disabled
              style="margin-bottom: 1%"
              >{{ office_zip }}</b-form-input
            >
            <b-form-input
              v-model="office_address_base"
              placeholder=" "
              disabled
              style="margin-bottom: 1%"
              >{{ office_address_base }}</b-form-input
            >
            <b-form-input
              v-model="office_address_detail"
              placeholder=" "
              style="margin-bottom: 1%"
              >{{ office_address_detail }}</b-form-input
            >
            <b-button @click="AddressApi(2)"> </b-button>
          </b-form-group>

          <hr />

          <b-button style="width: 150px; margin-top: 3%" @click="signupProcess">Sign Up</b-button>
          <div id="message" style="color: red"></div>
        </b-card>
      </b-col>
      <b-col cols="3"></b-col>
    </b-row>
  </div>
</template>

<script>
import SignUpHeader from "./SignUpHeader.vue";
export default {
  components: { SignUpHeader },
  computed: {
    idState() {
      if (this.id.length < 3) {
        //document.getElementById('input-id-feedback').innerText = "ID .";
        return false;
      } else if (this.id.length == 3) {
        document.getElementById("input-id-feedback").innerText = "ID .";
        return false;
      } else {
        document.getElementById("input-id-feedback").innerText = "   .";
        this.$store.dispatch("CEHCKEXIST", {
          id: this.id,
        });
        return !this.$store.state.checkexist;
      }
    },
    passState() {
      return this.pass.length > 3 ? true : false;
    },
    pass2State() {
      if (this.pass2.length <= 3) {
        return false;
      } else if (this.pass2.length == 3) {
        document.getElementById("input-pass2-feedback").innerText = " .";
        return false;
      } else {
        document.getElementById("input-pass2-feedback").innerText = "  .";
        return this.pass2 == this.pass && this.pass2 != "" ? true : false;
      }
    },
  },
  mounted() {
    const script = document.createElement("script");
    script.src = "//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js";
    document.head.appendChild(script);
  },
  data() {
    return {
      id: "",
      pass: "",
      pass2: "",
      name: "",
      email: "",
      address_base: "",
      address_detail: "",
      zip: "",
      tel: "",
      office_zip: "",
      office_address_base: "",
      office_address_detail: "",
      scoreCheck: "",
      car: "",
      pet: "",
    };
  },
  methods: {
    AddressApi(choice) {
      new window.daum.Postcode({
        oncomplete: (data) => {
          //        .

          //       .
          //      ('') ,    .
          let fullRoadAddr = data.roadAddress; //  
          let extraRoadAddr = ""; //    

          //    . ( )
          //     "//" .
          if (data.bname !== "" && /[||]$/g.test(data.bname)) {
            extraRoadAddr += data.bname;
          }

          //  ,   .
          if (data.buildingName !== "" && data.apartment === "Y") {
            extraRoadAddr += extraRoadAddr !== "" ? ", " + data.buildingName : data.buildingName;
          }

          // ,     ,     .
          if (extraRoadAddr !== "") {
            extraRoadAddr = " (" + extraRoadAddr + ")";
          }

          // ,        .
          if (fullRoadAddr !== "") {
            fullRoadAddr += extraRoadAddr;
          }

          //      .

          if (choice == 1) {
            this.zip = data.zonecode; //5  
            this.address_base = fullRoadAddr;
          }

          if (choice == 2) {
            this.office_zip = data.zonecode; //5  
            this.office_address_base = fullRoadAddr;
          }
        },
      }).open();
    },
    signupProcess() {
      if (
        this.id == "" ||
        this.pass == "" ||
        this.pass2 == "" ||
        this.pass != this.pass2 ||
        this.name == "" ||
        this.zip == "" ||
        this.address_base == "" ||
        this.address_detail == "" ||
        this.tel == "" ||
        this.$store.state.checkexist
      ) {
        document.getElementById("message").innerText = "   .";
      } else {
        this.$store
          .dispatch("SIGNUP", {
            id: this.id,
            pass: this.pass,
            name: this.name,
            email: this.email,
            address_base: this.address_base,
            address_detail: this.address_detail,
            zip: this.zip,
            tel: this.tel,
            office_zip: this.office_zip,
            office_address_base: this.address_base,
            office_address_detail: this.address_detail,
            scoreCheck: this.scoreCheck,
            car: this.car,
            pet: this.pet,
          })
          .then(() => {
            location.href = "/";
          });
      }
    },
  },
};
</script>

<style>
#signup {
  width: 100%;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-bottom: 4%;
}

#sign-card {
  margin-top: 2%;
  margin-bottom: 2%;
}

#select-info {
  color: #2f3b52;
  margin: 2%;
}

#message {
  margin: 2%;
}
</style>
