<template>
  <div id="apt-list">
    <result-header></result-header>
    <section>
      <b-row>
        <b-col></b-col>
        <b-col cols="8">
          <div id="result-table">
            <b-table
              striped
              hover
              :items="aptlist"
              :per-page="perPage"
              :current-page="currentPage"
              :fields="fields"
              @row-clicked="rowClick"
            ></b-table>
            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              align="center"
              style="margin-top: 2%; margin-bottom: 2%"
            ></b-pagination>
          </div>
        </b-col>
        <b-col></b-col>
      </b-row>
    </section>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ResultHeader from "../header/ResultHeader.vue";

export default {
  components: { ResultHeader },
  created() {
    var dong = this.$route.params.dong;
    //this.$route.params.dong;
    this.$store.dispatch("SEARCH", { dong: dong });
  },
  data() {
    return {
      currentPage: 1, //  
      perPage: 5, //   
      // bootstrap 'b-table'  
      fields: [
        {
          key: "no",
          label: "",
        },
        {
          key: "dong",
          label: "",
        },
        {
          key: "aptName",
          label: "",
        },
        {
          key: "code",
          label: "",
        },
        {
          key: "dealAmount",
          label: "",
        },
        {
          key: "buildYear",
          label: "",
        },
        {
          key: "dealYear",
          label: "",
        },
        {
          key: "dealMonth",
          label: "",
        },
        {
          key: "dealDay",
          label: "",
        },
        {
          key: "area",
          label: "",
        },
        {
          key: "floor",
          label: "",
        },
      ],
    };
  },
  computed: {
    ...mapGetters(["aptlist"]),
    rows() {
      return this.aptlist.length;
    },
  },
  methods: {
    rowClick(item) {
      this.$router.push({
        path: `/detail/${item.dong}/${item.aptName}`,
      });
      //       .
      /*
				this.$store
					.dispatch("DETAIL", { dong: item.dong, aptName: item.aptName })
					.then(() => {
						console.log(":", this.$store.state.aptdetail);
						this.$router.push({
							path: `/detail/${item.dong}/${item.aptName}`,
						});
					}); */
    },
  },
};
</script>

<style>
h1 {
  color: #2f3b52;
  font-weight: 900;
  margin: 2.5rem 0 1.5rem;
}
img {
  margin-top: 8%;
}
#result-table {
  margin-top: 3%;
  margin-bottom: 5%;
}
</style>
